import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-modulo-create',
  templateUrl: 'modulo-create.html',
})

export class ModuloCreatePage {
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public grupo		     : any;
  public titulo		     : any;

  public index		     : any;
  public icono		     : any;
  public estado		     : any;
  public nivel		     : any;
  idempresa:any;
  public vgrupo  :any=[];
  private _ID                  : String;
  public pageTitle           	: string;
  pactualizacion:boolean=false;

  isDisablednit :any;
  constructor( public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {

    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
     // Define form validation - VERY basic!
     this.form = this._FB.group({
        nombre     : ['', Validators.required],
        grupo     : ['', Validators.required],
        titulo     : ['', Validators.required],
      
        index     : ['', Validators.required],
        icono    : ['', Validators.required],
        nivel    : ['', Validators.required],
        estado     : ['', Validators.required]
       
     });

     this.authService.getReg('modulo-grupo','datosfijos').then((result) => {
      this.vgrupo=result;
    }, (err) => {  
    this.msj.showError3('Error',err.error);
  });

   //  this.vgrupo=[{"id":"USAC","nombre":"USAC"},{"id":"BUSES","nombre":"BUSES"},{"id":"TICKES","nombre":"TICKES"}];

   
   this.pactualizacion=this.navParams.get("actualizacion")

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {
    if(this.navParams.get("record")==null)
    {

      this.authService.getReg('todos/orden/'+this.idempresa,'modulos').then((data) => {
        if(data)
        {
          var aa:any=data;
           
          this.index=aa.orden;
          this.index=this.index+10;
        }
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });


      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      this.isDisablednit =true
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.titulo		=	this.navParams.data.record.titulo;
       this.grupo		=	this.navParams.data.record.grupo;
    
       this.index		=	this.navParams.data.record.index ;
       this.icono		=	this.navParams.data.record.icono;
       this.estado		=	this.navParams.data.record.estado;
       this.nivel	=	this.navParams.data.record.nivel;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }

  

  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }



     let   options       : any	     = { 
       idempresa:this.idempresa,
     nombre : this.form.controls['nombre'].value,
     grupo: this.form.controls['grupo'].value,
     titulo: this.form.controls['titulo'].value,
     componente: this.form.controls['titulo'].value,
     tabcomponente: this.form.controls['titulo'].value,
     index: this.form.controls['index'].value,
     icono: this.form.controls['icono'].value,
     estado : this.form.controls['estado'].value,
     nivel : this.form.controls['nivel'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' modulos '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'modulos').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'modulos').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

  
 

  
  

}



